body {
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: white;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.tabs button {
  padding: 10px 20px;
  margin: 0 10px;
  cursor: pointer;
  border: 2px solid #ccc;
  background-color: #f0f0f0;
  transition: background-color 0.3s;
}

.tabs button.active {
  background-color: #3e8e41; /* Зеленый цвет для активной вкладки */
  color: white;
}

.tabs button:hover {
  background-color: #ddd;
}

.click-tab-content, .upgrade-tab-content {
  text-align: center;
  padding: 20px;
}

.energy-status {
  position: fixed;
  bottom: 10px;
  left: 10px;
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border-radius: 5px;
}

.energy-status .energy-icon {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.score-board, .energy-board {
  margin-bottom: 20px;
  text-align: center;
}

.score-board p, .energy-board p {
  font-size: 18px;
}

.energy-board {
  margin-bottom: 20px;
}

.energy-icon {
  width: 30px;
  height: 30px;
}

.image-container {
  margin-bottom: 20px;
}

.clickable-image {
  width: 200px;
  height: auto;
  cursor: pointer;
}

.upgrade-container {
  margin-top: 20px;
}

.upgrade-container button {
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 10px;
  width: 250px;
  transition: background-color 0.3s;
}

.upgrade-container button:hover {
  background-color: #45a049;
}

.energy-upgrades {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.energy-upgrades button {
  padding: 10px;
  background-color: #008CBA;
  color: white;
  border: none;
  font-size: 16px;
  cursor: pointer;
  margin: 5px;
  width: 250px;
  transition: background-color 0.3s;
}

.energy-upgrades button:hover {
  background-color: #007b8f;
}
